
body{
  background-color: #18BC9C;
}
a {
  color: #8dc6ff;
}
a:hover {
  color: #1301ff;
}
.logo {
  width: 211px;
  margin: -38px;
}
.router-link-exact-active {
  background-color: #25313e !important;
}
.nav-menu{
  background-color: #2C3E50;
}
.nav-menu ul {
  height: 100%;
  text-align: left;
}
.nav-menu li {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: 100%;
}
.nav-menu a {
  padding: 38px;
  font-size: 20px;
  text-decoration: none;
  color: #FFFFFF;
  text-transform: uppercase;
  text-align: center;
  background: #2c3e50;
}
.nav-menu a:hover {
  background-color: #25313e;
}
.nav-current {
  background-color: #25313e;
}
.nav-disabled {
  background-color: rgb(57, 71, 82) !important;
  color: #787d82 !important;
  cursor: context-menu;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
@media print
{
.no-print, .no-print * {
    display: none !important;
}
#contract-section {
    padding: 0 !important;
    font-size: 1.4rem !important;
}
#contract-section h1{
    font-size: 1.63rem !important;
}
#contract-section h2{
    font-size: 1.51rem !important;
}
}
@page {
  margin: 2cm 2cm 2cm 4cm;
}
/*@page document-page{*/
  /*mso-page-orientation: landscape;*/
  /*size: 841.95pt 595.35pt; !* EU A4 *!*/
  /*!* size:11.0in 8.5in; *! !* US Letter *!*/
/*}*/
/*div.document-page {*/
  /*page: document-page;*/
/*}*/
#contract-section {
  /*font-family: 'Courier New', Courier, monospace;*/
  /*font-family: 'Times New Roman', Times, serif;*/
  font-family: 'Times New Roman', Times, serif;
  font-size: 12pt;
  white-space: pre-wrap;
  /*padding: 30mm 20mm 20mm 30mm;*/
  padding: 20mm 20mm 20mm 40mm;
  width: 210mm;
  margin: 0 auto;
  text-align: justify;
  /*float: left;*/
  background-color: #FFFFFF;
  display: table;
  table-layout: fixed;
}
#contract-section .no-doc {
}
#contract-section h1 {
  font-size: 14pt;
}
#contract-section h2 {
  font-size: 13pt;
}
#contract-section .page-break-warning{
  background-color: #859c98;
  text-align: center;
}
#contract-section .page-break{
  /*page-break-before: always !important;*/
  page-break-after: always !important;
  /*break-before: page !important;*/
  /*break-after: page !important;*/
  clear: both;
}
#contract-section .list{
  list-style-type: none;
  margin-left: 40px;
  /*text-indent: -23px;*/
  margin-bottom: 1rem;
}
#contract-section p {
  margin-bottom: 1rem;
}
#contract-section .numeric-list{
  list-style-type: none;
  text-indent: -3px;
}
#contract-section .circle-list{
  list-style-type: circle;
}
#contract-section .square-list{
  list-style-type: square;
}
#contract-section .title-center,
#contract-section .subtitle-center,
#contract-section .paragraph-center{
  text-align: center;
}



